import React from "react"
import { graphql } from "gatsby"

import Card from "react-bootstrap/Card"

import Layout from "../components/layout"
import SEO from "../components/seo"

const NotebooksPage = ({ data }) => (
  <Layout>
    <SEO title="Notebooks" />
    <div>
      <h1
        style={{
          display: `inline-block`,
          borderBottom: `4px solid`,
          fontFamily: `IM Fell DW Pica`,
          fontSize: `301%`,
        }}
      >
        Notebooks
      </h1>
      {data.allMdx.edges.map(({ node }) => (
        <div key={node.id}>
          <Card style={{ boxShadow: `8px 8px 5px grey` }}>
            <Card.Body>
              <Card.Link
                style={{
                  all: `inherit`,
                  cursor: `pointer`,
                }}
                href={node.fields.slug}
              >
                <Card.Title
                  style={{
                    marginBottom: `0.25`,
                    font: `italic bold 200% Merriweather`,
                  }}
                >
                  {node.frontmatter.title}
                  <p
                    style={{
                      font: `normal normal 40% Arial`,
                    }}
                  >
                    {node.frontmatter.date} &#183; {node.timeToRead} min read
                  </p>
                </Card.Title>
              </Card.Link>
            </Card.Body>
          </Card>
          <br></br>
        </div>
      ))}
    </div>
  </Layout>
)

export default NotebooksPage

export const query = graphql`
  query {
    allMdx(
      sort: { fields: [frontmatter___date], order: DESC }
      filter: { fields: { slug: { regex: "/notebooks/" } } }
    ) {
      totalCount
      edges {
        node {
          id
          frontmatter {
            title
            date(formatString: "MMMM DD, YYYY")
            author
            notebook
          }
          headings {
            value
            depth
          }
          fields {
            slug
          }
          timeToRead
        }
      }
    }
  }
`
